import { Dispatch, SetStateAction } from "react"
import { FileRowType } from "@rp/common/utils"
import { useFileActions } from "@rp/react/web/common/files"
import { SelectedFile } from "../../Files"

type HandleFileRowCheckboxChangeFuncProps = {
  actions: ReturnType<typeof useFileActions>["actions"]
  checked: boolean
  isSelected: boolean
  isThisFileOpen: boolean
  item: FileRowType
  openChatPane: () => void
  setSelectedFiles: Dispatch<SetStateAction<Array<SelectedFile>>>
  shared: boolean
}

function handleFileRowCheckboxChangeFunc({
  actions,
  checked,
  isSelected,
  isThisFileOpen,
  item,
  openChatPane,
  setSelectedFiles,
  shared,
}: HandleFileRowCheckboxChangeFuncProps): void {
  if (isSelected && isThisFileOpen) {
    actions.close.onClick()
  }

  if (!isSelected) {
    actions.open.onClick(item)
    openChatPane()
  }
  setSelectedFiles(prev => {
    return checked
      ? [...prev, { id: item.id, shared: !!shared }]
      : prev.filter(i => i.id !== item.id)
  })
}

export { handleFileRowCheckboxChangeFunc }
